/* Write your CSS here */
.container {
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;

  align-items: center;
}

.container2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}
.container3 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 50%;
  justify-content: space-around;
}
.search {
  margin-bottom: 25px;
  background-color: #f1f5f9;
  background-image: url('https://assets.ccbp.in/frontend/react-js/destinations-search-icon-img.png');
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: 170px;
  padding-left: 12px;
  padding-right: 32px;
  padding-top: 9px;
  padding-bottom: 9px;
  border-style: none;
  border-radius: 10px;
  border-width: 0;
}
.search:hover {
  border-style: none;
}
.searcg:focus {
  border-style: none;
}
